//改变主题的方法
function changeTheme(element, themeClass) {
    if (!element || !themeClass) {
        return;
    }
    var classString = element.className;
    var nameIndex = classString.indexOf(themeClass);
    if (nameIndex === -1) {
        classString = themeClass;
    } else {
        classString = null;
    }
    element.className = classString;
}
